import React, { useState } from "react";
import { Link } from "react-router-dom";
import { TextField, Text, PrimaryButton, Spinner, SpinnerSize } from "office-ui-fabric-react";
import { SearchBox } from "office-ui-fabric-react/lib/SearchBox";
import style from "./home.module.css";

/**
 * Home Component contains a search bar to initiate movie browsing.
 * Filters search results based on input into search bar.
 *
 * @param {*} props
 */
const Login = props => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    const [fetching, setFetching] = useState(false);

    const url = "https://movie-browser-api.herokuapp.com/api/login";
    const opts = {
        method: "POST",
    }

    const postLogin = () => {
        if (email.trim().toUpperCase() === "" || password.trim().toUpperCase() === "") return;
        opts.body = {
            email,
            password
        }
        setFetching(true);
        fetch(url, opts)
            .then(data => data.json())
            .then(data => {
                console.log(data);
                if (data.jwt) {
                    localStorage.set('JWT', data.jwt);
                    props.setAuthorized(true);
                } else {
                    setErrorMessage("Invalid email or password.. use the placeholder text");
                }
                setFetching(false);

            })
            .catch(err => {
                setErrorMessage("Invalid email or password.. use the placeholder text");
                setFetching(false);
            })

    }
    return (
        <main className={style.OuterWrapper}>
            <section style={{
                padding: "25px",
                background: "rgba(0,0,0,0.95)",
                borderRadius: "10px",
                border: "2px solid teal",
                boxShadow: "0px 0px 20px rgba(0,0,0,0.75)"
            }}>
                <TextField label="Email" placeholder="al@ace.ca" requried onChange={(e, value) => setEmail(value)} value={email} />
                <TextField type="password" label="Password" placeholder="mypassword" required onChange={(e, value) => setPassword(value)} value={password} />
                <Text>{errorMessage}</Text>
                <PrimaryButton styles={{ root: { margin: "10px" } }} disabled={fetching} onClick={() => postLogin()}>{fetching ? <Spinner size={SpinnerSize.small} /> : "Login"}</PrimaryButton>
            </section>
        </main>
    );
};

export default Login;
